<template>
  <v-container fluid @click="handleClick" :class="[selected ? 'selected' : 'normal']">
    <v-row dense class="d-flex">
      <v-col>
        <v-row fluid dense>
          <v-col>
            <h2>{{ title }}</h2>
          </v-col>
        </v-row>
        <v-row fluid dense>
          <v-col class="card-section-left-bottom">
            <v-icon>mdi-calendar-range</v-icon>
            Berakhir dalam: {{ endTimeString }}
          </v-col>
          <v-col class="card-section-left-bottom">
            <v-icon>mdi-clock-outline</v-icon>
            Waktu Pengerjaan: {{ workingTime }} menit
          </v-col>
        </v-row>
      </v-col>
      <v-col class="d-flex flex-align-center" cols="2">
        <img
          :src="require('@/assets/arrow_link_right_63px.svg')"
          alt
          class="align-self-center card-link"
        />
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import moment from 'moment/src/moment'
export default {
  moment,
  props: {
    title: String,
    endTime: String,
    workingTime: Number,
    selectedCard: String,
    id: Number
  },
  computed: {
    selected() {
      return this.title === this.selectedCard;
    },
    endTimeString() {
      const now = moment();
      const end = moment(this.endTime);
      const duration = moment.duration(end.diff(now));

      if (duration.months() > 0) {
        return `${duration.months()} bulan`;
      }
      if (duration.days() > 0) {
        return `${duration.days()} hari`;
      }
      if (duration.hours() > 0) {
        return `${duration.hours()} jam`;
      }
      return `${duration.minutes()} menit`;
    }
  },
  methods: {
    handleClick() {
      this.$emit("cardSelect", this.title, 1);
    }
  }
};
</script>

<style lang="scss" scoped>
.normal {
  border: 3px solid rgba(0, 0, 0, 0);
}
.selected {
  border: 3px solid #3ea48f;
  background: rgba(62, 164, 143, 0.3);
}

.card-section-left-bottom {
  padding-right: 0;
}

.card-link {
  height: 100%;
  max-height: 5em;
}
</style>
